import idbs from '../../../services/indexedDBService.js';

const namespaced = true;

const state = {
    patterns: []
};

const getters = {
    all: (state) => {
        return state.patterns;
    }
};

const actions = {
    async setPatterns ({ commit }) {
        try {
            let data = await idbs.getAllByCreated('patterns', 'url');

            if (data === null) data = [];

            commit('SET_PATTERNS', data);
        } catch (error) {
            console.error('store getAllByCreated error', error);
            // The value in storage was invalid or corrupt so just set it to blank
            const data = [];
            commit('SET_PATTERNS', data);
        }
    }
};

const mutations = {
    SET_PATTERNS: (state, payload) => {
        state.patterns = payload;
    }
};

export default {
    namespaced,
    state,
    getters,
    mutations,
    actions
};
