const namespaced = true;

const state = {
    colorScheme: null
};

const getters = {
    colorScheme: (state) => {
        return state.colorScheme;
    }
};

const actions = {
    async setColorScheme ({ commit }, payload) {
        let data = payload;
        if (payload) {
            localStorage.setItem('setting-color-scheme', data);
        } else {
            data = localStorage.getItem('setting-color-scheme');
        }

        return commit('SET_COLOR_SCHEME', data);
    }
};

const mutations = {
    SET_COLOR_SCHEME: (state, payload) => {
        state.colorScheme = payload;
    },

    RESET_COLOR_SCHEME: (state) => {
        state.colorScheme = null;
    }
};

export default {
    namespaced,
    state,
    getters,
    mutations,
    actions
};
