import Vue from 'vue';
import Vuex from 'vuex';
import settings from './modules/settings.mjs';
import aside from './modules/aside.mjs';
import pattern from './modules/pattern.mjs';
import modal from './modules/modal.mjs';

Vue.use(Vuex);

const debug = process.env.NODE_ENV !== 'production';

const store = new Vuex.Store({
    namespaced: true,
    modules: {
        settings,
        aside,
        pattern,
        modal
    },
    strict: debug
});

export { store };
