const namespaced = true;

const state = {
    active: false
};

const getters = {
    active: (state) => {
        return state.active;
    }
};

const actions = {
    async changeActive ({ commit }) {
        return commit('CHANGE_ACTIVE');
    }
};

const mutations = {
    CHANGE_ACTIVE: (state) => {
        state.active = !state.active;
    }
};

export default {
    namespaced,
    state,
    getters,
    mutations,
    actions
};
