<template>
    <div id="app">
        <icons-app></icons-app>
        <router-view v-bind:key="$route.fullPath"/>
    </div>
</template>

<script>
import IconsApp from './assets/icons/App.vue';
const lsNameColorScheme = 'setting-color-scheme';

export default {
    name: 'app',

    components: {
        IconsApp
    },

    data () {
        return {
            matchMedia: window.matchMedia('(prefers-color-scheme: dark)'),
            settingColorScheme: null,
            prefersColorScheme: null
        };
    },

    async mounted () {
        await this.$store.dispatch('settings/setColorScheme');

        this.settingColorScheme = this.$store.getters['settings/colorScheme'];
        this.prefersColorScheme = this.settingColorScheme;

        if (this.prefersColorScheme === null) {
            this.handlePrefersColorScheme(this.matchMedia);
            this.matchMedia.addListener(this.handlePrefersColorScheme);
        }

        window.addEventListener(
            'storage',
            this.handleSettingColorScheme,
            false
        );
    },

    methods: {
        handlePrefersColorScheme (event) {
            this.prefersColorScheme = event.matches ? 'dark' : '';

            const bodyElement = document.querySelector('body');
            bodyElement.setAttribute(
                'data-prefers-color-scheme',
                this.prefersColorScheme
            );
        },

        handleSettingColorScheme (event) {
            if (event.key === lsNameColorScheme && event.newValue !== null) {
                this.settingColorScheme =
                    this.$store.getters['settings/colorScheme'];

                this.prefersColorScheme = this.settingColorScheme;

                const bodyElement = document.querySelector('body');
                bodyElement.setAttribute(
                    'data-prefers-color-scheme',
                    this.prefersColorScheme
                );

                this.matchMedia.removeListener(this.handlePrefersColorScheme);
            } else {
                this.prefersColorScheme = this.matchMedia.matches ? 'dark' : '';
                const bodyElement = document.querySelector('body');
                bodyElement.setAttribute(
                    'data-prefers-color-scheme',
                    this.prefersColorScheme
                );

                this.matchMedia.addListener(this.handlePrefersColorScheme);
            }
        }
    },

    beforeDestroy () {
        if (this.settingColorScheme === null) {
            this.matchMedia.removeListener(this.handlePrefersColorScheme);
        }

        window.removeEventListener('storage', this.handleSettingColorScheme);
    }

};
</script>

