import { openDB } from 'idb';

const dbPromise = async () => {
    const db = await openDB('pattern-code', 1, {
        upgrade (db) {
        // Create a store of objects
            const store = db.createObjectStore('patterns', {
                // The 'id' property of the object will be the key.
                keyPath: 'id',
                // If it isn't explicitly set, create a value by auto incrementing.
                autoIncrement: true
            });
            store.createIndex('url', 'url');
            // Create an index on the 'created' property of the objects.
            store.createIndex('created', 'created');
        }
    });

    return db;
};

const getAllByCreated = async (storeName, storeKey) => {
    try {
        const db = await dbPromise();
        const data = await db.getAll(storeName);
        const latestByDate = data.filter((item, index, self) => {
            const myIndex = self.findIndex((element, index, array) => {
                return new Date(element.created).getTime() >
                       new Date(item.created).getTime() &&
                       element.url === item.url;
            });

            return myIndex <= 0;
        });

        return latestByDate;
    } catch (error) {
        console.error('getAllByCreated error: ', error);
    }
};

const getAllFromIndexCreated = async (storeName, storeKey, key) => {
    try {
        const db = await dbPromise();
        const data = await db.getAllFromIndex(storeName, storeKey, key);
        const latestByDate = data.filter((item, index, self) => {
            const myIndex = self.findIndex((element, index, array) => {
                return new Date(element.created).getTime() >
                       new Date(item.created).getTime();
            });

            return myIndex <= 0;
        });

        return latestByDate[0];
    } catch (error) {
        console.error('getAllFromIndexCreated error: ', error);
    }
};

const getAllFromIndex = async (storeName, storeKey, key) => {
    try {
        const db = await dbPromise();
        const data = await db.getAllFromIndex(storeName, storeKey, key);

        return data;
    } catch (error) {
        console.error('getAllFromIndex error', error);
    }
};

const add = async (storeName, data) => {
    try {
        const db = await dbPromise();

        await db.add(storeName, data);

        return data;
    } catch (error) {
        console.error('add error: ', error);
    }
};

const remove = async (storeName, storeKey, key) => {
    try {
        const patterns = await getAllFromIndex(storeName, storeKey, key);

        return patterns.forEach(async (pattern) => {
            const db = await dbPromise();
            await db.delete(storeName, pattern.id);
        });
    } catch (error) {
        console.error('delete error: ', error);
    }
};

export default {
    getAllByCreated,
    getAllFromIndexCreated,
    add,
    remove
};
