const namespaced = true;

const state = {
    display: false,
    type: {
        option: false,
        save: false,
        rename: false,
        patternSave: false,
        patternDelete: false,
        sectionRemove: null
    }
};

const getters = {
    display: (state) => {
        return state.display;
    },

    typePatternSave: (state) => {
        return state.type.patternSave;
    },

    typePatternDelete: (state) => {
        return state.type.patternDelete;
    },

    typeSectionRemove: (state) => {
        return state.type.sectionRemove;
    }
};

const actions = {
    async openPatternSave ({ commit }) {
        return commit('OPEN_PATTERN_SAVE');
    },

    async openPatternDelete ({ commit }) {
        return commit('OPEN_PATTERN_DELETE');
    },

    async openSectionRemove ({ commit }, payload) {
        return commit('OPEN_SECTION_REMOVE', payload);
    },

    async close ({ commit }) {
        return commit('CLOSE');
    }
};

const mutations = {
    OPEN_PATTERN_SAVE: (state) => {
        state.display = true;
        state.type.patternSave = true;
    },

    OPEN_PATTERN_DELETE: (state) => {
        state.display = true;
        state.type.patternDelete = true;
    },

    OPEN_SECTION_REMOVE: (state, payload) => {
        state.display = true;
        state.type.sectionRemove = payload;
    },

    CLOSE: (state) => {
        state.display = false;
        state.type.option = false;
        state.type.save = false;
        state.type.rename = false;
        state.type.patternDelete = false;
        state.type.sectionRemove = null;
    }
};

export default {
    namespaced,
    state,
    getters,
    mutations,
    actions
};
